import React from 'react';
import '../../assets/css/coming-soon.css';
import ComingSoonImg from '../../assets/img/coming-soon.jpg';
import logo from '../../assets/img/logo_1.png';

const ComingSoon = () => {
    return (
        <div className="coming-soon-wrapper" style={{ backgroundImage: `url(${ComingSoonImg})` }}>
        <div className="coming-soon-content">
          <img src={logo} alt="Logo" className="logo" />
          <h1 className="app-title">Morail's Creation</h1>
          <h1 className="title">Coming Soon</h1>
          <p className="subtitle">We're working hard to finish the development of this site. Stay tuned!</p>
        </div>
      </div>
    );
}

export default ComingSoon;